import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import yaml from "js-yaml";
import YAMLViewer from "./YAMLViewer";


const SlideCreator = () => {
    const [slides, setSlides] = useState([
        {
            quoteText:  "Timing isn't always everything.",
            quoteAuthor: 'Anonymous Swiss watchmaker',
            description: "Timing can often emerge as the decisive element in a number of critical game circumstances.",
            xgid: 'XGID=----bBBBBAB-A-AaabbbbcAA--:0:0:1:66:0:0:0:0:10',
            hint: 'Getting the answer right here means you’ve turned on your sense of timing in the game',
            choices: ['{{player}}', '{{opponent}}'],
            correct: 2,
            rank: "2, 1, 3",
            slideType: 'board',
            showComponent: true,
            showBestMove: true,
            showDice: true,
        },
    ]);

    const [generatedYaml, setGeneratedYaml] = useState(''); // For storing the YAML output

    const addChoice = (index) => {
        const newSlides = [...slides];
        newSlides[index].choices.push('');
        setSlides(newSlides);
    };

    const updateSlide = (index, field, value) => {
        const newSlides = [...slides];
        newSlides[index][field] = value;
        setSlides(newSlides);
    };

    const updateChoice = (slideIndex, choiceIndex, value) => {
        const newSlides = [...slides];
        newSlides[slideIndex].choices[choiceIndex] = value;
        setSlides(newSlides);
    };

    const addSlide = () => {
        setSlides([
            ...slides,
            {
                quoteText: '',
                quoteAuthor: '',
                description: '',
                xgid: '',
                hint: '',
                choices: [],
                correct: 0,
                rank: "1, 2, 3",
                slideType: '',
                showComponent: true,
                showBestMove: true,
                showDice: true,
            },
        ]);
    };
    const generateCustomYAML = (slides) => {
        // Helper function to format fields with block scalars
        const formatBlockScalar = (text, isQuote = false) => {
            if (!text.trim()) return ''; // Return empty for blank fields
    
            // Handle quote formatting with '>' and no '>' for the author line
            if (isQuote) {
                const lines = text.split('\n');
                const formattedLines = lines.map((line, index) => {
                    if (index === lines.length - 1 && line.startsWith('_')) {
                        return `      ${line}`; // Quote author should not have a '>'
                    }
                    return `      > ${line}`; // Every other line should have '>'
                });
                return `|\n${formattedLines.join('\n')}`;
            }
    
            // Format other block scalar content with correct indentation
            const formattedText = text.split('\n').map(line => `      ${line}`).join('\n');
            return `|\n${formattedText}`; // Return formatted with the pipe character
        };
    
        const yamlOutput = slides.map(slide => {
            let slideYaml = '  - quote:';
    
            // Handle the explicitly controlled fields
            slideYaml += slide.quoteText
                ? ` ${formatBlockScalar(slide.quoteText + `\n_${slide.quoteAuthor.trim()}_`, true)}`
                : '';
    
            slideYaml += `\n    description: ${slide.description ? formatBlockScalar(slide.description) : ''}`;
            slideYaml += `\n    xgid: ${slide.xgid || ''}`;
            slideYaml += `\n    hint: ${slide.hint ? formatBlockScalar(slide.hint) : ''}`;
    
            if (slide.choices.length > 0) {
                slideYaml += `\n    choices:`;
                slide.choices.forEach(choice => {
                    slideYaml += `\n      - |\n        ${choice.trim()}`;
                });
            } else {
                slideYaml += `\n    choices:`;
            }
    
            slideYaml += `\n    correct: ${slide.correct}`;
            slideYaml += slide.rank ? `\n    rank: ${slide.rank}` : '';
    
            // Process any other fields generically
            Object.keys(slide).forEach(key => {
                if (
                    ![
                        'quoteText',
                        'quoteAuthor',
                        'description',
                        'xgid',
                        'hint',
                        'choices',
                        'correct',
                        'rank',
                    ].includes(key)
                ) {
                    const value = slide[key];
                    if (typeof value === 'boolean' || typeof value === 'number') {
                        slideYaml += `\n    ${key}: ${value}`;
                    } else if (typeof value === 'string' && value.trim() !== '') {
                        slideYaml += `\n    ${key}: ${value}`;
                    }
                }
            });
    
            return slideYaml;
        }).join('\n');
    
        return `slides:\n${yamlOutput}`;
    };
    
    const generateYAML = () => {
        const yamlOutput = generateCustomYAML(slides);
        setGeneratedYaml(yamlOutput);
        return
        // const yamlSlides = slides.map(slide => {
        //     return {
        //         quote: slide.quoteText.trim() ? slide.quoteText.trim() + `\n\n_${slide.quoteAuthor.trim()}_` : '',
        //         description: slide.description.trim() || '',
        //         xgid: slide.xgid || '',
        //         hint: slide.hint.trim() || '',
        //         choices: slide.choices.length > 0 ? slide.choices.map(choice => choice.trim()) : [],
        //         correct: slide.correct > 0 ? slide.correct : null,
        //         rank: slide.rank !== "" ? slide.rank : null,
        //         slideType: slide.slideType || undefined,
        //         showComponent: slide.showComponent,
        //         showBestMove: slide.showBestMove,
        //         showDice: slide.showDice,
        //     };
        // });

        // // Clean up null or undefined fields
        // const cleanedYamlSlides = yamlSlides.map(slide =>
        //     Object.fromEntries(Object.entries(slide).filter(([_, v]) => v !== undefined && v !== null))
        // );

        // // Use js-yaml to dump the cleaned data, ensuring block scalar formatting
        // const yamlOutput = yaml.dump({ slides: cleanedYamlSlides }, {
        //     lineWidth: -1, // Ensure no line wrapping
        //     noCompatMode: true,
        //     replacer: (key, value) => {
        //         // Force block scalar for description and hint
        //         if (typeof value === 'string' && ['description', 'hint'].includes(key)) {
        //             return `\n${value}`; // Ensure this string is treated as a block scalar
        //         }
        //         // Force block scalar for quote and ensure each line is prefixed with '>'
        //         if (typeof value === 'string' && key === 'quote') {
        //             const lines = value.split('\n');
        //             const formattedQuote = lines.map(line => `> ${line}`).join('\n');
        //             return `\n${formattedQuote}`;
        //         }
        //         // For choices array, ensure each element is treated as a block scalar
        //         if (Array.isArray(value) && key === 'choices') {
        //             return value.map(item => `\n${item}`);
        //         }

        //         return value; // Return other values as they are
        //     }
        // });

        // setGeneratedYaml(yamlOutput);
    };

    return (
        <div className="container-fluid">
            <div className="row">
                {/* Left column - Input Editor */}
                <div className="col-md-6 p-4">
                    <h3>Create Slides</h3>
                    {slides.map((slide, index) => (
                        <div key={index} className="card mb-4">
                            <div className="card-body">
                                <h5>Slide {index + 1}</h5>
                                <div className="mb-3">
                                    <label>Quote Text:</label>
                                    <textarea
                                        className="form-control"
                                        value={slide.quoteText}
                                        onChange={(e) => updateSlide(index, 'quoteText', e.target.value)}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label>Quote Author:</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={slide.quoteAuthor}
                                        onChange={(e) => updateSlide(index, 'quoteAuthor', e.target.value)}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label>Description:</label>
                                    <textarea
                                        rows={8}
                                        className="form-control"
                                        value={slide.description}
                                        onChange={(e) => updateSlide(index, 'description', e.target.value)}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label>XGID:</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={slide.xgid}
                                        onChange={(e) => updateSlide(index, 'xgid', e.target.value)}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label>Hint:</label>
                                    <textarea
                                        className="form-control"
                                        value={slide.hint}
                                        onChange={(e) => updateSlide(index, 'hint', e.target.value)}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label>Slide Type:</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={slide.slideType}
                                        onChange={(e) => updateSlide(index, 'slideType', e.target.value)}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label>Choices (Array):</label>
                                    {slide.choices.map((choice, choiceIndex) => (
                                        <input
                                            key={choiceIndex}
                                            type="text"
                                            className="form-control mb-2"
                                            value={choice}
                                            onChange={(e) => updateChoice(index, choiceIndex, e.target.value)}
                                        />
                                    ))}
                                    <button
                                        type="button"
                                        className="btn btn-secondary"
                                        onClick={() => addChoice(index)}
                                    >
                                        Add Choice
                                    </button>
                                </div>
                                <div className="mb-3">
                                    <label>Correct Choice (Index):</label>
                                    <input
                                        type="number"
                                        className="form-control"
                                        value={slide.correct}
                                        onChange={(e) => updateSlide(index, 'correct', e.target.value)}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label>Rank Choice (Index):</label>
                                    <input
                                        type="string"
                                        className="form-control"
                                        value={slide.rank}
                                        onChange={(e) => updateSlide(index, 'rank', e.target.value)}
                                    />
                                </div>
                                <div className="form-check">
                                    <input
                                        type="checkbox"
                                        className="form-check-input"
                                        checked={slide.showComponent}
                                        onChange={(e) => updateSlide(index, 'showComponent', e.target.checked)}
                                    />
                                    <label className="form-check-label">Show Component</label>
                                </div>
                                <div className="form-check">
                                    <input
                                        type="checkbox"
                                        className="form-check-input"
                                        checked={slide.showBestMove}
                                        onChange={(e) => updateSlide(index, 'showBestMove', e.target.checked)}
                                    />
                                    <label className="form-check-label">Show Best Move</label>
                                </div>
                                <div className="form-check">
                                    <input
                                        type="checkbox"
                                        className="form-check-input"
                                        checked={slide.showDice}
                                        onChange={(e) => updateSlide(index, 'showDice', e.target.checked)}
                                    />
                                    <label className="form-check-label">Show Dice</label>
                                </div>
                            </div>
                        </div>
                    ))}
                    <button className="btn btn-primary" onClick={addSlide}>
                        Add Slide
                    </button>
                    <button className="btn btn-success mt-3" onClick={generateYAML}>
                        Generate YAML
                    </button>
                </div>

                {/* Right column - YAML Output */}
                <YAMLViewer generatedYaml={generatedYaml} />
            </div>
        </div>
    );
};

export default SlideCreator;
