import React, {useState, useEffect } from 'react';
import CodeMirror from '@uiw/react-codemirror';
import { yaml } from '@codemirror/lang-yaml';
import 'bootstrap/dist/css/bootstrap.min.css';

const YAMLViewer = ({ generatedYaml }) => {
    const [message, setMessage] = useState('')
    const handleCopy = () => {
        navigator.clipboard.writeText(generatedYaml)
            .then(() => setMessage("yaml copied to clipboard"))
            .catch(err => setMessage('Failed to copy YAML! Pause, call Alex'));
    };
    useEffect(() => {
        if (message) {
            const timer = setTimeout(() => {
                setMessage('');
            }, 4000); // Clears the message after 5 seconds

            return () => clearTimeout(timer); // Clean up the timer on component unmount or when message changes
        }
    }, [message]);

    return (
        <div className="col-md-6 p-4">
            <h3>YAML Output</h3>
            <CodeMirror
                value={generatedYaml}
                height="400px"
                extensions={[yaml()]}
                theme="dark" // You can use 'dark' or other themes if needed
                readOnly={true}
                basicSetup={{ lineNumbers: true }}
                style={{ border: '1px solid #ccc', borderRadius: '4px' }}
            />
            <button className="btn btn-primary mt-3" onClick={handleCopy}>
                Copy YAML
            </button>
            <div>{message}</div>
        </div>
    );
};

export default YAMLViewer;
